.contact{
    background-color: white;
    display: flex;

    .left{
        flex:1;
        overflow: hidden;

        img{
            height: 100%;
        }
    }
    .right{
        flex:1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2{
            font-size: 50px;
        }

        form{
            width: 100%;
            height: 45%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            

            input{
                width: 500px;
                height: 30px;
                font-size: 14px;
            }

            textarea{
                width: 500px;
                height: 200px;
                font-size: 14px;
            }

            button{
                width: 250px;
                height: 50px;
                background-color: crimson;
                color:white;
                border: none;
                border-radius: 10px;
                font-weight: 500;
                cursor: pointer;

                &:focus{
                    outline: none;
                }
            }
            span{
                color: green;
            }
        }
    }
}